/* -- DEMONSTRATION PURPOSES ONLY -- */
/* ---------------------------------------------- */

// Bootstrap module functions
// ---------------------------------------------------------------------------------
@import "bootstrap/scss/functions";



// Colors system
// ---------------------------------------------------------------------------------
@import "../colors/palettes/default";
@import "../colors/schemes/default";
@import "../colors/variables";



// Bootstrap variables
// ---------------------------------------------------------------------------------
@import "../bootstrap/variables";
@import "../nifty/variables";



// Demo purpose
// ---------------------------------------------------------------------------------
// Dropdown
._dm-dropdown .dropdown-menu {
   margin: 0;
   opacity: 1;
   pointer-events: all;
   position: static;
   touch-action: auto;
   transform: none;
   visibility: visible;
}



// Settings
// ---------------------------------------------------------------------------------
._dm-settings-container {
   content-visibility: auto;
   display: none;
   pointer-events: none;

   > .offcanvas-body {
      max-height: 95vh;
      overflow-y: auto;
   }
}


@media ( min-width: 990px ) {

   // Button Gear
   // ---------------------------------------------------------------------------------
   ._dm-btn-settings {
      inset: 1.5rem auto auto 0;
      position: absolute;
      transform: translateX( - 100% );
      visibility: visible;
   }

   // Settings container
   // ---------------------------------------------------------------------------------
   ._dm-settings-container.offcanvas {
      border-left : 5px solid var( --#{ $prefix }danger );
      bottom: auto;
      content-visibility: visible;
      display: flex;
      pointer-events: unset;
      top: 50% !important;
      width: auto;
      z-index: 99999;
      transform: translateX(100%) translateY(-50%);

      &.show:not(.hiding), &.showing {
         transform: translateX(0) translateY(-50%);
      }

      // .offcanvas-body {
      //    overflow: visible;
      // }

      img[loading=lazy] {
         display: block;
      }
   }



   // Settings body
   // ---------------------------------------------------------------------------------
   ._dm-settings-container__content {
      width: 1100px;
   }

   #_dm-offcanvas.show {
      z-index: 99999;

      ~ .offcanvas-backdrop.show {
         z-index: 99998;
      }
   }

   // Boxed Background Images
   // ---------------------------------------------------------------------------------
   // The collection of background images for the boxed layout
   ._dm-boxbg {
      border-radius: var( --#{ $prefix }border-radius ) var( --#{ $prefix }border-radius ) 0 0;
      content-visibility: auto;
      height: auto !important;
      margin: 0 auto;
      width: 1024px;
      z-index: 99999;

      &.show {
         box-shadow: var( --#{ $prefix }box-shadow-lg );
      }
   }

   ._dm-boxbg__img-container {
      border-radius: var( --#{ $prefix }border-radius );

      ._dm-boxbg__thumb {
         border-radius: #{ calc( var( --#{ $prefix }border-radius ) / 2 )};
         display: block;
         margin: .1875rem;
         transition: transform .25s;
         width: calc( 25% - ( .1875rem * 2));

         img {
            border-radius: #{ calc( var( --#{ $prefix }border-radius ) / 2 )};
         }

         &:hover:not(.active) {
            box-shadow: 0 0 0 .1875rem $white, 0 0 1rem rgba( $black, .75 );
            position: relative;
            transform: scale( 1.5 );
            z-index: 1;
         }

         &.active {
            cursor: default;
            filter: grayscale( 1 );
            transform: scale(.9);

            img { opacity: .5 ;}

            &:after {
               content: "";
               position: absolute;
               width: 1.2rem;
               height: .7rem;
               border-width: 0 0 4px 4px;
               border-style: solid;
               border-color: $white;
               inset: 50% 0 0 50%;
               transform: translateX(-50%) translateY(-50%) rotate(-45deg);
               transform-origin: center center;
            }
         }
      }
   }



   // Color Scheme Buttons
   // ---------------------------------------------------------------------------------
   ._dm-colorSchemes {
      appearance: none;
      border-radius: .75rem;
      border: 0;
      height: 1.5rem;
      margin-bottom: .2rem;
      margin-inline: .2rem;
      width: 1.5rem;
      transition: transform .25s, width .25s !important;

      &:hover {
         box-shadow: 0 0 .5rem rgba( $black, .15 );
         transform: scale(1.1);
      }

      &.active {
         position: relative;
         width: 2rem;

         &:before {
            border-color: $white;
            border-style: solid;
            border-width: 0 0 3px 3px;
            content: "";
            height: .45rem;
            inset: 50% 0 0 50%;
            position: absolute;
            transform-origin: center center;
            transform: translateX(-50%) translateY(-50%) rotate(-45deg);
            width: .7rem;
         }
      }
   }



   // Color Modes
   // ---------------------------------------------------------------------------------
   ._dm-colorModeBtn {
      border: 0;
      transition: transform .15s !important;

      &:not(.active):hover {
         transform: scale( 1.1 );
      }

      &.active {
         pointer-events: none;
         position: relative;

         > img{
            opacity: .5;
         }

         &:before {
            content: "";
            position: absolute;
            background-color: var( --#{ $prefix }primary );
            width: 27px;
            height: 27px;
            inset: 50% 50% auto auto;
            border-radius: 1rem;
            transform: translateX( 50% ) translateY( -50% );
         }

         &:after {
            border-color: $white;
            border-style: solid;
            border-width: 0 0 3px 3px;
            content: "";
            height: .45rem;
            inset: 50% 0 0 50%;
            position: absolute;
            transform-origin: center center;
            transform: translateX(-50%) translateY(-50%) rotate(-45deg);
            width: .7rem;
         }
      }
   }


   // Generate colors for preview
   // ---------------------------------------------------------------------------------
   $nifty-theme-colors : (
      "gray"   : #98a3b0,
      "navy"   : #265689,
      "ocean"  : #59b3f0,
      "lime"   : #7abf1e,
      "violet" : #b788f0,
      "orange" : #f9ab24,
      "teal"   : #35cabc,
      "corn"   : #ffde2a,
      "cherry" : #ec4d6c,
      "coffee" : #ea916a,
      "pear"   : #d2649d,
      "night"  : #4b5158
   );

   @each $name, $bg in $nifty-theme-colors {
      ._dm-bg-#{$name} {
         background-color: $bg;
      }
   }
}
